    background: #000;
    opacity: 0;
    width: 15vw;
    height: 40vh;
    transition: all 0.3s ease;
}

.business__overlay-text-wrapper{
    display: grid;
    grid-template-rows: 30% 30% 30%;
    font-size: 2rem;
    font-weight: 600;
    opacity: 1;
}

.business__text{
    text-align: center;
    padding: 1.1rem;
    margin-top: 4vh;
}

.business__overlay:hover{
    cursor: pointer;
    opacity: 0.6;
}

.business__post:hover{
    cursor: pointer;
}

@media screen and (max-width: 1024px){
    .business__post-container{
        display: flex;
        flex-direction: column;
        width: 100vw;
    }

    .business__profile-image{
        width: 78vw;
    }

    .business__overlay{
        width: 80vw;
    }

    .business__profile-name{
        width: 80vw;
    }

    .business__text{
        margin-top: 2vh;
    }
}

@media screen and (width: 1024px){
    .business__tutor-head{
        font-size: 6rem;
    }

    .business__profile-name{
        font-size: 2rem;
    }

    .business__overlay-text-wrapper{
        line-height: 6rem;
        font-size: 4rem;
    }
}
